@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Montserrat&family=Roboto:wght@300;400;500&family=Shrikhand&display=swap');
.all_collapse{
    width: 90%;
    background-color: #1d1d1d;
    border-radius: 10px;
    margin: 0px 20px 0px 15px;
}

.collapse{
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    color: #cfd0d0;
    height: 50px;
    margin-left: 5px;
    font-family: 'Caveat', cursive;
    font-size: 20px;
}
.arrow_down{
    height: 15px;
    margin-top: 4px;
    margin-right: 5px;
}
.collapse_text{
    color: #cfd0d0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.image_collapse{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.icone_github{
    height: 80px;
}
.photo_collapse{
    width: 100%;
    object-fit: cover;
    height: 80px;
}
a{
    text-decoration: none;
}
.read_me{
    color: #cfd0d0;
    text-decoration: none
}
@media screen and (min-width: 792px) {
    .icone_github{
        height: 125px;
    }
    .photo_collapse{
        height: 125px;
    }
}